<template>
  <div class="rank" ref="rank">
     <scroll :lists="topList" ref='scroll' class="toplist">
         <ul>
             <li class="item" @click="selectItem(item)" v-for="item in topList" :key='item.id'>
                 <div class="icon">
                     <img src="" v-lazy="item.picUrl" width='100' height='100' alt="">
                 </div>
                 <ul class="songlist">
                     <li class="song" v-for="(song,index) in item.songList" :key='song.id'>
                         <span>{{index + 1}}</span>
                         <span>{{song.songname}} - {{song.singername}}</span>
                     </li>
                     <span></span>
                     <span></span>
                 </ul>
             </li>
         </ul>
         <div class="loading-container" v-show="!topList.length"> 
              <loading></loading>
         </div>
     </scroll>
     <router-view></router-view>
  </div>
</template>

 <script type="text/ecmascript-6">
  
  import {getTopList} from '@a/rank'
  import{ERR_OK} from 'assets/js/config'

  import scroll from '@c/scroll/scroll'
  import loading from '@c/loading/loading'
  import { playlistMixin } from 'assets/js/mixin'

  export default {
      mixins:[playlistMixin],
      data(){
          return{
              topList:[]
          }
      },
      components:{
         scroll,
         loading
      },
      methods:{
        handlePlaylist(playlist){
          const bottom = playlist.length > 0 ? '60px' : 0
          this.$refs.rank.style.bottom =  bottom
          // 重新调用scroll组件的refresh()方法
          this.$refs.scroll.refresh()
        },
        _getTopList(){
            getTopList().then((res) =>{
               if(res.code == ERR_OK){
                   console.log('111111')
                   console.log(res.data.topList)
                   this.topList =  res.data.topList
               }
            })
        },
        selectItem(item){
            this.$router.push({
                // path:`/rank/topList/${item.id}`,
                name:'topList',
                params:{
                    id:item.id
                }
            })          
            this.$store.dispatch('topList',item)
        }
      },
      created(){
          this._getTopList()
      }
  }
</script>

<style scoped lang="scss">
  .rank{
    position: fixed;
    width: 100%;
    top: 88px;
    bottom: 0;
    .toplist{
      height: 100%;
      overflow: hidden;
      .item{
        display: flex;
        margin: 0 20px;
        padding-top: 20px;
        height: 100px;
        &:last-child{
          padding-bottom: 20px;
        }
        .icon{
          flex: 0 0 100px;
          width: 100px;
          height: 100px;
        }
        .songlist{
          flex: 1;
          display: flex;
          flex-direction: column;
          justify-content: center;
          padding: 0 20px;
          height: 100px;
          overflow: hidden;
          background: $color-highlight-background;
          color: $color-text-d;
          font-size: $font-size-small;
          .song{
            @include text-overflow();
            line-height: 26px;
          }
        }
      }
      .loading-container{
        position: absolute;
        width: 100%;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
</style>


